// components/dynamic-zone/how-it-works/card.tsx
'use client';

import React from 'react';
import Link from 'next/link';
import { motion, useReducedMotion } from 'framer-motion';
import { ArrowRight, type LucideIcon } from 'lucide-react';

export const Card = ({
  mode,
  title,
  description,
  index,
  badge,
  icon: Icon,
  ctaText,
  ctaHref,
}: {
  mode: 'panel' | 'stack';
  title: string;
  description: string;
  index: number;
  badge: string;
  icon: LucideIcon;
  ctaText?: string;
  ctaHref?: string;
}) => {
  const reduce = useReducedMotion();
  const isPanel = mode === 'panel';

  const enter = {
    hidden: { opacity: 0, y: reduce ? 0 : 10 },
    show: {
      opacity: 1,
      y: 0,
      transition: reduce ? { duration: 0.2 } : { duration: 0.6, ease: [0.22, 1, 0.36, 1] },
    },
  };

  return (
    <motion.div
      variants={enter}
      initial={isPanel ? false : 'hidden'}
      whileInView={isPanel ? undefined : 'show'}
      viewport={isPanel ? undefined : { once: true, amount: 0.35 }}
      className={[
        'relative rounded-3xl',
        'border border-[rgb(var(--lightblack-rgb)/0.10)]',
        // “white” surface
        'bg-[rgb(var(--paper-rgb)/0.92)]',
        isPanel ? 'px-10 py-10 md:px-14 md:py-12' : 'px-7 py-7',
      ].join(' ')}
      style={{
        boxShadow: '0 30px 90px rgb(var(--lightblack-rgb)/0.08)',
      }}
    >
      {/* subtle top sheen */}
      <div
        aria-hidden
        className="pointer-events-none absolute inset-0 rounded-3xl"
        style={{
          background:
            'radial-gradient(800px circle at 20% 0%, rgb(var(--paper-rgb)/0.70), transparent 55%)',
          opacity: 0.75,
        }}
      />

      {/* top meta */}
      <div className="relative z-10 flex items-center justify-between gap-6">
        <div className="flex items-center gap-3">
          <div
            className="grid h-10 w-10 place-items-center rounded-full bg-[rgb(var(--paper-rgb)/0.96)]"
            style={{
              border: '1px solid rgb(var(--lightblack-rgb)/0.12)',
              boxShadow: 'inset 0 0 0 6px rgb(var(--paper-rgb)/0.55)',
            }}
          >
            <Icon className="h-[18px] w-[18px] text-[var(--muted)]" style={{ opacity: 0.85 }} aria-hidden />
          </div>

          <div className="flex flex-col">
            <span className="text-[10px] tracking-[0.30em] uppercase text-[var(--secondary)]" style={{ opacity: 0.82 }}>
              {badge}
            </span>
            <span className="text-xs font-medium text-[var(--secondary)]" style={{ opacity: 0.72 }}>
              {String(index).padStart(2, '0')}
            </span>
          </div>
        </div>

        <div className="hidden md:block h-px w-24 bg-[var(--lightblack)]" style={{ opacity: 0.10 }} aria-hidden />
      </div>

      {/* content */}
      <h3
        className={[
          'relative z-10 mt-6 font-semibold tracking-tight text-[var(--lightblack)]',
          isPanel ? 'text-3xl md:text-[34px]' : 'text-xl',
        ].join(' ')}
      >
        {title}
      </h3>

      <p
        className={[
          'relative z-10 mt-4 text-[var(--secondary)] leading-[1.95]',
          isPanel ? 'text-[15px] md:text-base max-w-3xl' : 'text-sm',
        ].join(' ')}
      >
        {description}
      </p>

      {/* CTA (minimal, muted only as accent) */}
      {ctaText && ctaHref ? (
        <div className="relative z-10 mt-6">
          <Link
            href={ctaHref}
            className="group inline-flex items-center gap-2 text-sm font-medium text-[var(--lightblack)] focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-[var(--muted)] focus-visible:ring-offset-2 focus-visible:ring-offset-[rgb(var(--paper-rgb)/0.95)]"
            aria-label={ctaText}
          >
            <span className="relative">
              {ctaText}
              <span
                aria-hidden
                className="absolute left-0 -bottom-1 h-px w-full origin-left scale-x-0 bg-[var(--muted)] transition-transform duration-300 group-hover:scale-x-100"
                style={{ opacity: 0.55 }}
              />
            </span>
            <motion.span
              aria-hidden
              className="inline-flex text-[var(--muted)]"
              initial={false}
              whileHover={reduce ? undefined : { x: 3 }}
              transition={reduce ? undefined : { duration: 0.22, ease: [0.22, 1, 0.36, 1] }}
              style={{ opacity: 0.8 }}
            >
              <ArrowRight className="h-4 w-4" />
            </motion.span>
          </Link>
        </div>
      ) : null}
    </motion.div>
  );
};
