// components/dynamic-zone/how-it-works/index.tsx
'use client';

import React, { useEffect, useMemo, useRef, useState } from 'react';
import { motion, useReducedMotion } from 'framer-motion';
import { Calendar, Coffee, Diamond, Sparkles, type LucideIcon } from 'lucide-react';

import { Container } from '../../container';
import { Heading } from '../../elements/heading';
import { Subheading } from '../../elements/subheading';
import { Card } from './card';

type StepInput = {
  title?: string | null;
  description?: string | null;
  ctaText?: string | null;
  ctaHref?: string | null;
  badge?: string | null;
};

type Step = {
  title: string;
  description: string;
  badge: string;
  icon: LucideIcon;
  ctaText?: string;
  ctaHref?: string;
};

type Pt = { x: number; y: number };

const bezier = (t: number, p0: Pt, p1: Pt, p2: Pt): Pt => {
  const u = 1 - t;
  return {
    x: u * u * p0.x + 2 * u * t * p1.x + t * t * p2.x,
    y: u * u * p0.y + 2 * u * t * p1.y + t * t * p2.y,
  };
};

const bezierTangent = (t: number, p0: Pt, p1: Pt, p2: Pt): Pt => {
  // B'(t) = 2(1-t)(P1-P0) + 2t(P2-P1)
  const u = 1 - t;
  return {
    x: 2 * u * (p1.x - p0.x) + 2 * t * (p2.x - p1.x),
    y: 2 * u * (p1.y - p0.y) + 2 * t * (p2.y - p1.y),
  };
};

const clamp01 = (n: number) => Math.max(0, Math.min(1, n));

export const HowItWorks = ({
  heading,
  sub_heading,
  steps,
}: {
  heading?: string | null;
  sub_heading?: string | null;
  steps?: StepInput[] | null;
}) => {
  const reduce = useReducedMotion();

  const baseSteps: Step[] = useMemo(
    () => [
      {
        title: 'Időpont Foglalás',
        description:
          'Kezdje meg útját a megújulás felé. Válassza ki az Önnek legmegfelelőbb időpontot kényelmes online rendszerünkön keresztül, vagy hívjon minket bizalommal egyedi igényeivel.',
        badge: 'ELSŐ LÉPÉS',
        icon: Calendar,
        ctaText: 'Foglalás Most',
        ctaHref: '#',
      },
      {
        title: 'Konzultáció',
        description:
          'Személyes találkozó szakértőinkkel, ahol részletesen felmérjük igényeit, bőrét és stílusát. Egy közös beszélgetés során megalkotjuk az Önre szabott, egyedi kezelési tervet.',
        badge: 'MÁSODIK LÉPÉS',
        icon: Coffee,
      },
      {
        title: 'Szolgáltatás',
        description:
          'Dőljön hátra és élvezze a prémium kezelés nyugodt, luxus környezetében. A legkiválóbb anyagok és legmodernebb technikák felhasználásával varázsoljuk újjá megjelenését.',
        badge: 'HARMADIK LÉPÉS',
        icon: Diamond,
      },
      {
        title: 'Utóápolás',
        description:
          'A törődés nem ér véget a szalonban. Szakértői tanácsokkal és professzionális termékekkel látjuk el, hogy a tartós, ragyogó eredményt otthon is megőrizhesse.',
        badge: 'NEGYEDIK LÉPÉS',
        icon: Sparkles,
        ctaText: 'Kapcsolatfelvétel',
        ctaHref: '#',
      },
    ],
    []
  );

  const resolvedSteps: Step[] = useMemo(() => {
    const inSteps = Array.isArray(steps) ? steps : [];
    return baseSteps.map((s, i) => {
      const incoming = inSteps[i] ?? {};
      return {
        ...s,
        title: (incoming.title ?? s.title) as string,
        description: (incoming.description ?? s.description) as string,
        badge: (incoming.badge ?? s.badge) as string,
        ctaText: (incoming.ctaText ?? s.ctaText) ?? undefined,
        ctaHref: (incoming.ctaHref ?? s.ctaHref) ?? undefined,
      };
    });
  }, [baseSteps, steps]);

  const [active, setActive] = useState(0);
  const [auto, setAuto] = useState(!reduce); // reduced-motion: default OFF
  const safeActive = Math.min(Math.max(active, 0), resolvedSteps.length - 1);
  const activeStep = resolvedSteps[safeActive];

  // Auto-loop
  useEffect(() => {
    if (reduce) return;
    if (!auto) return;
    const len = resolvedSteps.length;
    if (len <= 1) return;

    const id = window.setInterval(() => {
      setActive((a) => (a + 1) % len);
    }, 3200);

    return () => window.clearInterval(id);
  }, [auto, reduce, resolvedSteps.length]);

  // Arc measurement
  const arcRef = useRef<HTMLDivElement>(null);
  const [arcW, setArcW] = useState(0);

  useEffect(() => {
    const el = arcRef.current;
    if (!el) return;

    const ro = new ResizeObserver((entries) => {
      const w = entries?.[0]?.contentRect?.width ?? 0;
      setArcW(w);
    });
    ro.observe(el);
    return () => ro.disconnect();
  }, []);

  // Arc geometry (virtual canvas wider than viewport so it can "rotate" under the center marker)
  const arc = useMemo(() => {
    const viewportW = arcW || 1000;
    const virtualW = Math.max(viewportW * 1.65, viewportW + 420); // enough room to shift
    const H = 190;

    const pad = Math.max(52, virtualW * 0.08);
    const baseY = H - 64; // ends
    const peakY = 34; // middle peak (upwards arc)

    const p0 = { x: pad, y: baseY };
    const p1 = { x: virtualW / 2, y: peakY };
    const p2 = { x: virtualW - pad, y: baseY };

    const nodesT = resolvedSteps.length <= 1
      ? [0.5]
      : resolvedSteps.map((_, i) => {
          // keep away from extremes
          const raw = resolvedSteps.length === 1 ? 0.5 : i / (resolvedSteps.length - 1);
          return 0.08 + raw * 0.84;
        });

    const nodePts = nodesT.map((t) => bezier(t, p0, p1, p2));
    const activePt = nodePts[safeActive] ?? { x: virtualW / 2, y: baseY };

    // Shift the whole arc group so the active node sits under the center marker
    const centerMarkerX = viewportW / 2;
    const deltaX = centerMarkerX - activePt.x;

    return { viewportW, virtualW, H, p0, p1, p2, nodesT, nodePts, deltaX };
  }, [arcW, resolvedSteps, safeActive]);

  const ticks = useMemo(() => Array.from({ length: 72 }, (_, i) => i), []);

  const wrap = {
    hidden: { opacity: 0, y: reduce ? 0 : 10 },
    show: {
      opacity: 1,
      y: 0,
      transition: reduce ? { duration: 0.2 } : { duration: 0.75, ease: [0.22, 1, 0.36, 1] },
    },
  };

  return (
    <section
      style={
        {
          // required tokens
          ['--charcoal' as any]: '#E7E5E4',
          ['--lightblack' as any]: '#292624',
          ['--secondary' as any]: '#4f4c4c',
          ['--muted' as any]: '#B2933D',

          // rgb helpers
          ['--charcoal-rgb' as any]: '231 229 228',
          ['--lightblack-rgb' as any]: '41 38 36',
          ['--secondary-rgb' as any]: '79 76 76',
          ['--muted-rgb' as any]: '178 147 61',

          // “white” surface (only for panels)
          ['--paper-rgb' as any]: '255 255 255',
        } as React.CSSProperties
      }
      className={[
        'relative overflow-hidden bg-[var(--charcoal)] text-[var(--lightblack)]',
        // charcoal background, very subtle depth (NOT gold)
        "before:content-[''] before:absolute before:inset-0 before:pointer-events-none",
        'before:bg-[radial-gradient(900px_circle_at_50%_-12%,rgb(var(--lightblack-rgb)/0.06),transparent_58%),radial-gradient(720px_circle_at_50%_112%,rgb(var(--lightblack-rgb)/0.05),transparent_62%)]',
        "after:content-[''] after:absolute after:inset-0 after:pointer-events-none",
        'after:bg-[linear-gradient(to_bottom,rgb(var(--lightblack-rgb)/0.03),transparent_18%,transparent_82%,rgb(var(--lightblack-rgb)/0.03))]',
      ].join(' ')}
    >
      <Container className="relative z-10 py-20 md:py-28 max-w-7xl mx-auto">
        <motion.div variants={wrap} initial="hidden" whileInView="show" viewport={{ once: true, amount: 0.25 }}>
          {/* Eyebrow */}
          <div className="flex items-center justify-center">
            <div className="inline-flex items-center gap-4 text-[var(--secondary)]">
              <span aria-hidden className="h-px w-10 bg-[var(--lightblack)]" style={{ opacity: 0.18 }} />
              <span className="text-[11px] tracking-[0.32em] uppercase" style={{ opacity: 0.85 }}>
                Exkluzív Folyamat
              </span>
              <span aria-hidden className="h-px w-10 bg-[var(--lightblack)]" style={{ opacity: 0.18 }} />
            </div>
          </div>

          {/* Header */}
          <div className="mt-8 grid gap-8 md:grid-cols-2 md:items-end">
            <div>
              <Heading className="mx-auto max-w-3xl text-left text-[var(--lightblack)]">
                {heading ?? 'A szépség útja'}
                <span className="text-[var(--muted)]">.</span>
              </Heading>
              <div className="mt-5 h-px w-16 bg-[var(--lightblack)]" style={{ opacity: 0.14 }} />
            </div>

            {sub_heading ? (
              <Subheading className="mx-auto max-w-xl text-left text-[var(--secondary)]">
                {sub_heading}
              </Subheading>
            ) : null}
          </div>

          {/* Desktop: centered card + curved rotating timeline */}
          <div className="mt-12 md:mt-16 hidden md:block">
            {/* Centered card “towards the arc” */}
            <div className="mx-auto max-w-4xl">
              <Card
                mode="panel"
                index={safeActive + 1}
                title={activeStep.title}
                description={activeStep.description}
                badge={activeStep.badge}
                icon={activeStep.icon}
                ctaText={activeStep.ctaText}
                ctaHref={activeStep.ctaHref}
              />
            </div>

            {/* Arc viewport */}
            <div
              ref={arcRef}
              className={[
                // pull the arc closer to the card (towards it)
                '-mt-10',
                'relative mx-auto max-w-6xl',
                'rounded-3xl',
                'border border-[rgb(var(--lightblack-rgb)/0.10)]',
                // main background = charcoal; arc “surface” = white-ish
                'bg-[rgb(var(--paper-rgb)/0.78)]',
                'px-6 pt-8 pb-10',
                'overflow-hidden',
              ].join(' ')}
              style={{
                boxShadow: '0 28px 90px rgb(var(--lightblack-rgb)/0.08)',
              }}
            >
              {/* Auto-loop toggle (minimal, centered) */}
              <div className="flex items-center justify-center gap-4">
                <div className="text-sm font-medium text-[var(--secondary)]" style={{ opacity: 0.9 }}>
                  Auto-loop
                </div>

                <div
                  role="group"
                  aria-label="Auto-loop kapcsoló"
                  className="inline-flex rounded-full border border-[rgb(var(--lightblack-rgb)/0.10)] bg-[rgb(var(--paper-rgb)/0.75)] p-1"
                >
                  <button
                    type="button"
                    onClick={() => setAuto(true)}
                    className={[
                      'px-4 py-1.5 text-sm rounded-full',
                      'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-[var(--muted)] focus-visible:ring-offset-2 focus-visible:ring-offset-[var(--paper-rgb)/0.9]',
                      auto ? 'bg-[rgb(var(--paper-rgb)/0.96)] text-[var(--lightblack)]' : 'text-[var(--secondary)]',
                    ].join(' ')}
                  >
                    Yes
                  </button>
                  <button
                    type="button"
                    onClick={() => setAuto(false)}
                    className={[
                      'px-4 py-1.5 text-sm rounded-full',
                      'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-[var(--muted)] focus-visible:ring-offset-2 focus-visible:ring-offset-[var(--paper-rgb)/0.9]',
                      !auto ? 'bg-[rgb(var(--paper-rgb)/0.96)] text-[var(--lightblack)]' : 'text-[var(--secondary)]',
                    ].join(' ')}
                  >
                    No
                  </button>
                </div>
              </div>

              <div className="mt-7 text-center text-sm text-[var(--secondary)]" style={{ opacity: 0.7 }}>
                Kattints a pontokra – vagy kapcsold be az automatikus forgást.
              </div>

              {/* Center marker (fixed) */}
              <div aria-hidden className="pointer-events-none absolute left-1/2 top-0 bottom-0 w-px">
                <div
                  className="absolute top-[132px] h-[78px] w-px"
                  style={{
                    background: 'rgb(var(--muted-rgb)/0.50)',
                    transform: 'translateX(-0.5px)',
                  }}
                />
              </div>

              {/* Arc + ticks + nodes group (moves horizontally so the active point lands in center) */}
              <motion.div
                className="relative mt-10 h-[190px]"
                initial={false}
                animate={reduce ? { x: arc.deltaX } : { x: arc.deltaX }}
                transition={reduce ? { duration: 0 } : { type: 'spring', stiffness: 220, damping: 30 }}
                style={{ width: arc.virtualW }}
              >
                {/* The arc line (SVG for crisp curve) */}
                <svg
                  aria-hidden
                  width={arc.virtualW}
                  height={arc.H}
                  viewBox={`0 0 ${arc.virtualW} ${arc.H}`}
                  className="absolute inset-0"
                >
                  <path
                    d={`M ${arc.p0.x} ${arc.p0.y} Q ${arc.p1.x} ${arc.p1.y} ${arc.p2.x} ${arc.p2.y}`}
                    fill="none"
                    stroke="rgb(var(--lightblack-rgb)/0.16)"
                    strokeWidth="1"
                  />
                  {/* ultra subtle highlight */}
                  <path
                    d={`M ${arc.p0.x} ${arc.p0.y} Q ${arc.p1.x} ${arc.p1.y} ${arc.p2.x} ${arc.p2.y}`}
                    fill="none"
                    stroke="rgb(var(--paper-rgb)/0.65)"
                    strokeWidth="1"
                    style={{ transform: 'translateY(-0.6px)' }}
                  />
                </svg>

                {/* Ticks along the curve (perpendicular to tangent) */}
                {ticks.map((i) => {
                  const t = i / (ticks.length - 1);
                  const tt = 0.06 + t * 0.88; // keep inside ends
                  const p = bezier(tt, arc.p0, arc.p1, arc.p2);
                  const tan = bezierTangent(tt, arc.p0, arc.p1, arc.p2);
                  const ang = Math.atan2(tan.y, tan.x); // tangent
                  const normalDeg = ((ang + Math.PI / 2) * 180) / Math.PI;

                  const major = i % 18 === 0;
                  const mid = i % 9 === 0;
                  const len = major ? 22 : mid ? 16 : 10;

                  return (
                    <span
                      key={`tick-${i}`}
                      aria-hidden
                      className="absolute w-px bg-[var(--lightblack)]"
                      style={{
                        left: p.x,
                        top: p.y,
                        height: len,
                        opacity: major ? 0.22 : mid ? 0.16 : 0.12,
                        transform: `translate(-0.5px, -${len / 2}px) rotate(${normalDeg}deg)`,
                        transformOrigin: 'center',
                      }}
                    />
                  );
                })}

                {/* Nodes */}
                {resolvedSteps.map((s, i) => {
                  const p = arc.nodePts[i] ?? { x: arc.virtualW / 2, y: arc.H - 64 };
                  const isActive = i === safeActive;
                  const Icon = s.icon;

                  return (
                    <button
                      key={`node-${i}`}
                      type="button"
                      onClick={() => setActive(i)}
                      className="absolute"
                      style={{
                        left: p.x,
                        top: p.y,
                        transform: 'translate(-50%, -50%)',
                      }}
                      aria-label={`${String(i + 1).padStart(2, '0')} – ${s.title}`}
                    >
                      <motion.div
                        initial={false}
                        animate={
                          reduce
                            ? undefined
                            : isActive
                            ? { scale: 1, opacity: 1 }
                            : { scale: 0.98, opacity: 0.9 }
                        }
                        transition={reduce ? undefined : { duration: 0.22, ease: [0.22, 1, 0.36, 1] }}
                        className={[
                          'group grid h-11 w-11 place-items-center rounded-full',
                          'bg-[rgb(var(--paper-rgb)/0.92)]',
                          'focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-[var(--muted)] focus-visible:ring-offset-2 focus-visible:ring-offset-[rgb(var(--paper-rgb)/0.9)]',
                        ].join(' ')}
                        style={{
                          border: `1px solid rgb(var(--lightblack-rgb)/${isActive ? '0.18' : '0.12'})`,
                          boxShadow: `inset 0 0 0 7px rgb(var(--paper-rgb)/0.55)`,
                        }}
                      >
                        <Icon
                          className="h-[18px] w-[18px]"
                          aria-hidden
                          style={{
                            color: 'var(--muted)',
                            opacity: isActive ? 0.9 : 0.55,
                          }}
                        />
                      </motion.div>

                      <div
                        className="mt-2 text-[11px] font-medium text-[var(--secondary)]"
                        style={{ opacity: isActive ? 0.95 : 0.6 }}
                      >
                        {String(i + 1).padStart(2, '0')}
                      </div>
                    </button>
                  );
                })}
              </motion.div>

              {/* Centered title under the marker (stays centered, “dial” feeling) */}
              <div className="mt-2 text-center">
                <div className="text-base font-semibold tracking-tight text-[var(--lightblack)]">
                  {activeStep.title}
                </div>
                <div className="mt-1 text-[11px] tracking-[0.26em] uppercase text-[var(--secondary)]" style={{ opacity: 0.75 }}>
                  Milestone
                </div>
              </div>
            </div>
          </div>

          {/* Mobile: clean stacked (no curve) */}
          <div className="mt-12 md:hidden space-y-6">
            {resolvedSteps.map((s, i) => (
              <Card
                key={`m-${i}`}
                mode="stack"
                index={i + 1}
                title={s.title}
                description={s.description}
                badge={s.badge}
                icon={s.icon}
                ctaText={s.ctaText}
                ctaHref={s.ctaHref}
              />
            ))}
          </div>
        </motion.div>
      </Container>
    </section>
  );
};
